#contact-fixed-wrapper{
  width: 40px;
  height: 120px;
  position: fixed;
  right: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  background: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  z-index: 10000;
}

#phone-item{
  background: #252525;
}

#mail-item{
  background: #797979;
}

#facebook-item{
  background: #3A66B8;
}

#google-item{
  background: #EA4336;
}

.contact-item{
  -ms-flex: 1 1;
      flex: 1 1;
  background: red;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: white;
}

.contact-item:hover{
  cursor: pointer;
}



