header{
  background: #2D2D2D;
  height: 100vh;
}

button:focus{
  outline: 0
}

.particles{
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.button{
  padding: 15px;
  font-size: 15px;
  border-radius: 3px;
}

.center-section{
  text-align: center;
  padding: 25px;
}

.center-section h2{
  margin-bottom: 20px;
}

.center-section p{
  text-align: justify;
}

.center-section button{
  background-color: #e09e63;
  color: #ffffff;
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  border: none;
}

.center-section button:hover{
  background-color: #b17c4d;
  cursor: pointer;
  padding: 5px 10px 5px 10px;
}

.box-wrapper{
  margin-top: 30px;
  -ms-flex-align: center;
      align-items: center;
}

.box-item{
  display: inline-block;
  margin: 5px;
  width:29%;
  position: relative;
}

.box-item span{
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.77);
  z-index: 1000;
  position: absolute;
  top: 0;
  left: 0;
  color: #c4c4c4;
  text-align: justify;
  padding: 10px;
  visibility: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.prestations{
  border-top: 5px solid #E09E61;
  border-bottom: 5px solid #E09E61;
  background-color: #2D2D2D;
  padding-bottom: 40px;
}

#header-content-wrapper{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}

#logo-container{
  text-align: center;
  height: 90vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#logo-container h1{
  color: #dedede;
  letter-spacing: 1.1px;
  margin-top: 20px;
  font-size: 2em;
}

#box-item-1:hover span{
  visibility: visible;
}

#box-item-2:hover span{
  visibility: visible;
}

#box-item-3:hover span{
  visibility: visible;
}

header nav{
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  height: 10vh;
  border-bottom: 5px solid #a9774b;
}

header nav ul{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0;
}

header nav ul li{
  font-size: 18px;
  display: inline;
  color: #ffffff;
  padding: 20px;
  color: #a9774b;
  text-align: center;
  font-weight: bold;
}

header nav ul li:hover{
  color: #e09e63;
  cursor: pointer;
}


@media screen and (max-width: 425px) {
  #logo-container img{
    width: 185px;
  }
}

@media screen and (max-width: 767px){
  #home-page-header{
    display: none;
  }
  header{
    border-bottom: 4px solid #c17735;
  }
  .center-section button{
    margin-top: 20px;
  }
}

@media screen and (max-width: 768px){
  .box-container{
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .box-item{
    display: inherit;
    margin: 0 auto;
    margin-bottom: 10px;
    margin-top: 10px;
    width: 80%;
  }
}

.prestaModal{
  position: fixed; /* Stay in place */
  z-index: 100000; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100vh; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.prestations h2{
  color: #b5723c;
}

.presta-row{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.presta-item{
  width: 200px;
  height: 200px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
}

.presta-item:hover{
  cursor: pointer;
}

.presta-background{
  width: 100%;
  height: 80%;
  background-size: cover;
  background-position: center;
}

.presta-text{
  height: 20%;
  background: #b5723c;
  font-weight: bold;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}

#hidden-prestations{
  max-height: 0;
  -webkit-transition: -webkit-transform 2s linear;
  transition: -webkit-transform 2s linear;
  -o-transition: transform 2s linear;
  transition: transform 2s linear;
  transition: transform 2s linear, -webkit-transform 2s linear;
  overflow: hidden;
}

#hidden-prestations.slide-down{
  max-height: inherit;
}

#hidden-prestations.slide-up{
  max-height: 0px;
}

#button-container{
  margin-top: 20px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

#button-container button{
  border: 2px solid #ffffff;
  color: #ffffff;
  border-radius: 4px;
  background-color: transparent;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 20px;
}

#button-container button:hover{
  cursor: pointer;
}

#button-container .more{
  position: absolute;
  right: 50;
  font-size: 40px;
  color: white;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

#modal {
  width: 80%;
  max-height: 90%;
  background: #E8E8E8;
  margin: 0 auto;
  text-align: justify;
}

#modal h2{
  padding: 20px;
  border-bottom: 1px solid rgb(209, 209, 209);
  width: 90%;
}

#modalContent{
  padding: 20px;
  overflow: auto;
  max-height: 70%;
}

.more:hover{
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
}

@media screen and (max-width: 768px){
  .presta-item{
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
  }
}

#toTop:hover{
  cursor: pointer;
}



